.reservation-button-container {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 30px;
}

.reservation-button {
    background: linear-gradient(135deg, #28a745, #218838); /* Gradient green */
    color: white;
    padding: 15px 30px;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 10px;
    display: inline-block;
    transition: all 0.3s ease;
    border: 2px solid #1e7e34;
    box-shadow: 0px 5px 15px rgba(40, 167, 69, 0.4);
    letter-spacing: 1px;
}

.reservation-button:hover {
    background: linear-gradient(135deg, #218838, #1e7e34); /* Darker gradient */
    box-shadow: 0px 8px 20px rgba(40, 167, 69, 0.6);
    transform: scale(1.05);
}

.reservation-button:active {
    transform: scale(0.98);
    box-shadow: 0px 3px 10px rgba(40, 167, 69, 0.3);
}


.info-top-box {
    background-color: #f7f8fc;
    color: #333;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    margin: 20px 0;
    border-left: 5px solid rgb(237, 115, 1);
    font-size: 16px;
    line-height: 1.6;
    text-align: center;
    // text-align: left;
}
.info-top-box i {
    font-size: 20px;
    color: rgb(237, 115, 1);
    margin-right: 8px;

}
