.logo {
    color: white;
    font-size: 1.5 rem;
    padding: 0 2 rem;
    display: flex; // ✅ Ensure logo stays inline
    align-items: center;
}

.logo img{
    width: 60px;
}

.nav-wrap{
    display: flex;
    width: 100%;
    justify-content: space-between; 
    background: black;
    box-shadow: 3px 3px 5px rgb(0, 0, 0.1);
    ul{
        // width: 100px;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        li{
            height: 50px;
            a{
                height: 100%;
                padding: 0 30px;
                text-decoration: none;
                display: flex;
                align-items: center;
                color :white;
                font-size: 20px;
                border-radius: 5px; 
                transition: background-color 0.3s ease, color 0.3s ease;
            }
            a:hover {
                background-color: orange;
                color: black;
                font-weight: bold;
                /*transition: 0.4s ease-in-out;*/
            }
        }
        .search-icon {
            display: flex;
            align-items: center;
            gap: 10px;
        }
        
        .search-icon input {
            background: black;
            border: 1px solid #e3e3e3;
            color: white;
            padding: 5px 10px;
            border-radius: 5px;
            outline: none;
        }
        
        .search-icon input::placeholder {
            color: #aaa;
        }
        
        .search-icon button {
            background: none;
            border: none;
            cursor: pointer;
        }
        
        .search-icon svg {
            width: 24px;
            height: 24px;
            fill: white;
        }
    }
    .sidebar{
        position:fixed;
        top: 0;
        right: 0;
        height: 100vh;
        width: 300px;
        z-index: 999;
        background-color: rgba(6, 6, 6, 0.79);
        box-shadow: -10px 0 10px rgba(0,0,0,0.1);
        display: none;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        backdrop-filter: blur(10px);
        li{
            width: 100%;
        }
        a{
            width: 100%;
        }
    
    }
    
    .menu-button{
        display: none;
    }
}


// Search BAR
.search-form {
    display: flex;
    align-items: center;
    background-color: #000;
    padding: 0.4rem 0.8rem;
    border-radius: 8px;
    max-width: 200px;
    width: auto;
    margin: 0 auto;
    margin-right: 1rem;
    gap: 0.5rem;
    border: 1.5px solid #ffffff; /* White border */
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.2); /* Optional glow */
}
  
.search-form input[type="text"] {
    flex-grow: 1;
    background: transparent;
    border: none;
    outline: none;
    color: white;
    font-size: 1rem;
    padding: 0.4rem 0.2rem;
}
  
.search-form input[type="text"]::placeholder {
    color: #bbb;
}
  
.search-form button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Adjustments for mobile screens */
@media (max-width: 768px) {
    .hideOnMobile {
        display: none;
    }

    .nav-wrap .menu-button {
        display: block;
    }

    .search-form {
        justify-content: center;
        max-width: 70%; /* Limit width on mobile */
        margin-top: 0.5rem;
        margin-right: auto;
        margin-left: auto;
        padding: 0.3rem 0.6rem; /* Slightly less padding */
        border-radius: 6px;
        font-size: 0.9rem; /* Slightly smaller text */
    }

    .search-form input[type="text"] {
        font-size: 0.9rem;
    }

    .search-form button {
        padding: 5px; /* Smaller button size */
    }

    .search-form svg {
        width: 20px; /* Smaller icon */
        height: 20px;
    }
}