.top-articles-container {
    max-width: 600px;
    margin: 30px auto;
    font-family: Arial, sans-serif;
    padding: 0 15px;
}

.top-title-container {
    display: flex;
    align-items: center;
    background-color: #ff6600;
    padding: 10px 20px;
    border-radius: 8px;
    width: fit-content;
}

.top-title-container svg {
    fill: white;
    width: 28px;
    height: 28px;
    margin-right: 12px;
}

.top-title {
    color: white;
    font-size: 1.5em;
    margin: 0;
}

.top-articles-placeholder {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 15px;
    /* Lazy load if needed */
}

.top-article {
    display: flex;
    align-items: center;
    justify-content: space-between; 
    gap: 15px;
    padding: 15px;
    background: #f9f9f9;
    border-radius: 12px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    text-decoration: none;
    color: inherit;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
}

.top-article:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.top-article img {
    width: 80px;
    height: 80px;
    border-radius: 8px;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
}

.top-article .rank {
    font-size: 22px;
    font-weight: bold;
    color: #ff6600;
    min-width: 30px;
}

.top-article .text {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.top-article .title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

.top-article .link {
    font-size: 14px;
    color: #666;
}

.top-article .note {
    font-size: 16px;
    font-weight: bold;
    color: #ff6600;
    background: rgba(255, 102, 0, 0.1);
    padding: 5px 10px;
    border-radius: 8px;
    text-align: center;
    min-width: 60px; 
    text-align: right;
}

/* Mobile Optimization (Screen width < 600px) */
@media (max-width: 600px) {
    .top-article {
        flex-direction: row; /* Keep in a row for mobile */
        align-items: center;
        padding: 10px;
    }

    .top-article img {
        width: 60px;
        height: 60px;
    }

    .top-article .rank {
        font-size: 18px;
    }

    .top-article .text {
        flex-grow: 1;
        text-align: left;
    }

    .top-article .title {
        font-size: 14px;
    }

    .top-article .link {
        font-size: 12px;
    }

    .top-article .note {
        font-size: 14px;
        padding: 3px 6px;
        min-width: 50px; 
        text-align: right;
    }
}

.language-toggle {
    position: absolute;
    top: 100px;  // Increased from 20px to 60px for better spacing
    right: 20px;
    z-index: 100;
}

.language-button {
    background: #ff6600; // Matches the theme
    color: white;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 15px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.language-button:hover {
    background: #e65c00; // Slightly darker shade on hover
    transform: scale(1.05);
}