/* Centering the grid and limiting width to 2/3 of the screen */
.grid-wrapper_main {
    display: flex;
    justify-content: center;
}

.grid-container_main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    text-align: center;
    padding: 10px;
    width: 66%; /* 2/3 of the screen width */
    max-width: 1000px;
}

.grid-item_main {
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    width: 100%;
    height: 200px; /* Ensuring uniform image height */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.grid-item_main img {
    width: 100%;
    height: 100%; /* Ensuring all images have the same height */
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
    border-radius: 5px;
    display: block;
}

.grid-item_main:hover img {
    transform: scale(1.05);
}

.grid-item_main .overlay_main {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60px; /* Set fixed height for text overlay */
    background: rgba(0, 0, 0, 0.6);
    color: white;
    text-align: center;
    font-size: 1em;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}

.grid-item_main a {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: inherit;
    position: relative;
    width: 100%;
    height: 100%;
}

.flag-icon {
    width: 15px !important;  /* Force smaller width */
    height: 15px !important; /* Ensure proportional height */
    margin-right: 4px; /* Adjust spacing */
    vertical-align: middle;
    display: inline-block;
    object-fit: contain; /* Prevents stretching */
}

/* Mobile and tablet view adjustments */
@media (max-width: 768px) {
    .grid-container_main {
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
        width: 90%;
    }
    .grid-item_main {
        height: 200px; /* Adjust height for smaller screens */
    }
}

@media (max-width: 480px) {
    .grid-container_main {
        // grid-template-columns: 1fr;
        grid-template-columns: repeat(2, 1fr);
        gap: 6px;
        width: 100%;
    }
    .grid-item_main {
        height: 180px; /* Adjust height for mobile */
    }
}